import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';
import PageHeader from '../../../components/PageHeader/PageHeader';
import PageHeaderNav from '../../../components/PageHeadNav/PageHeadNav';
import { t } from '../../../i18n';
import { CHECKS_NAMESPACE, TABS } from '../constants';
import CheckOrder from './CheckOrder';
import OrdersHistory from './OrdersHistory';

class Feature extends PureComponent {
  _activateTab = tabId => {
    this.props.activateTab(tabId);
  };
  _getTabTitle = () => {
    const tab = TABS.filter(tab => tab.id === this.props.activeTab)[0];
    return tab.title;
  };
  renderActiveTab = () => {
    switch (this.props.activeTab) {
      case TABS[0].id:
        return <CheckOrder />;
      case TABS[1].id:
        return <OrdersHistory />;
      default:
        return <CheckOrder />;
    }
  };
  render() {
    return (
      <div className="page-content">
        <div className="page-content_side">
          <PageHeader text={t(this._getTabTitle())}>
            <li key="1">
              <Link to="/dashboard">
                {t(`${CHECKS_NAMESPACE}breadCrumbFirstLink`)}
              </Link>
            </li>
            <li key="2">
              <Link to="/checks">
                {t(`${CHECKS_NAMESPACE}breadCrumbSecondLink`)}
              </Link>
            </li>
            <li className="active" key="3">
              {t(this._getTabTitle())}
            </li>
          </PageHeader>
          <div className="page-content_inner">
            <PageHeaderNav>
              {TABS.map(tab => (
                <li key={tab.id}>
                  <Link
                    className={tab.id === this.props.activeTab ? 'active' : ''}
                    title={t(tab.title)}
                    to="#"
                    onClick={() => this._activateTab(tab.id)}
                  >
                    {t(tab.title)}
                  </Link>
                </li>
              ))}
            </PageHeaderNav>
            <div className="main-content container-fluid">
              {this.renderActiveTab()}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(Feature);
