import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import {
  CHECK_ORDER_STEP1_NAMESPACE,
  CHECK_ORDER_STEP2_NAMESPACE,
  CHECK_ORDER_STEP3_NAMESPACE,
} from '../constants';

const classNames = new Array(3);
const titles = [
  t(`${CHECK_ORDER_STEP1_NAMESPACE}chooseAccount`),
  t(`${CHECK_ORDER_STEP2_NAMESPACE}chooseBook`),
  t(`${CHECK_ORDER_STEP3_NAMESPACE}chooseBranch`),
];
class Stepper extends React.PureComponent {
  _renderSteps = () => {
    const { activeStep } = this.props;
    for (let i = 0; i < classNames.length; i++) {
      classNames[i] = i < activeStep - 1 ? 'complete' : '';
    }
    classNames[activeStep - 1] = 'active';
    return classNames.map((className, index) => (
      <li
        key={className + index}
        data-step={index.toString()}
        className={className}
      >
        <span className="text">{titles[index]}</span>
        <span className="chevron" />
      </li>
    ));
  };
  render() {
    return (
      <ul className="steps" style={{ 'margin-left': 0 }}>
        {this._renderSteps()}
      </ul>
    );
  }
}
const mapStateToProps = state => ({
  activeStep: state.Checks.activeStep,
});
export default withTranslation()(
  connect(
    mapStateToProps,
    null,
  )(Stepper),
);
