import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { Dropdown } from 'primereact/dropdown';
import { t } from '../../../i18n';
import {
  CHECK_ORDER_STEP1_NAMESPACE,
  CHECK_BOOKS_NAMESPACE,
} from '../constants';
import { step1 } from '../../../redux/checks/checks.actions';
import { getAccounts } from '../../../redux/accounts/accounts.actions';

const dropdownTemplate = option => (
  <>
    <span style={{ cursor: 'pointer' }}>{`${t(
      `${CHECK_ORDER_STEP1_NAMESPACE}checkingAccount`,
    )}: ${option.value}`}</span>
  </>
);
class CheckOrderStep1 extends PureComponent {
  state = {
    chosenAccount: null,
  };
  _handleSubmit = formSubmitEvent => {
    formSubmitEvent.preventDefault();
    this.props.step1({ accountNumber: this.state.chosenAccount });
  };
  componentDidMount() {
    if (this.props.accounts.length === 0) this.props.getAccounts();
  }
  render() {
    const accountsOptions = this.props.accounts.map(account => ({
      value: account.accountNumber,
      label: `${t(`${CHECK_ORDER_STEP1_NAMESPACE}checkingAccount`)}: ${
        account.accountNumber
      }`,
    }));
    return (
      <div className="step-pane active" data-step="1">
        <div className="content">
          <h3>{t(`${CHECK_ORDER_STEP1_NAMESPACE}title`)}</h3>
          <form onSubmit={this._handleSubmit}>
            <div className="form-group select-chequier">
              <Dropdown
                name="account"
                options={accountsOptions}
                value={this.state.chosenAccount}
                onChange={e => {
                  this.setState({ chosenAccount: e.value });
                }}
                placeholder={t(`${CHECK_ORDER_STEP1_NAMESPACE}selectAnAccount`)}
                itemTemplate={dropdownTemplate}
              />
            </div>
            <div className="form-group form-footer">
              <div className="align-center text-center col-md-12">
                <button
                  className="btn btn-default btn-space"
                  type="button"
                  disabled
                >
                  {t(`${CHECK_BOOKS_NAMESPACE}previous`)}
                </button>
                <button
                  className="btn btn-secondary btn-space wizard-next"
                  type="submit"
                  disabled={!this.state.chosenAccount}
                >
                  {t(`${CHECK_BOOKS_NAMESPACE}next`)}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  accounts: state.Accounts.accounts,
});
const mapDispatchToProps = dispatch => ({
  step1: step1Data => {
    dispatch(step1(step1Data));
  },
  getAccounts: () => {
    dispatch(getAccounts());
  },
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(CheckOrderStep1),
);
