import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Dropdown } from 'primereact/dropdown';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';
import {
  CHECK_ORDER_STEP3_NAMESPACE,
  CHECK_BOOKS_NAMESPACE,
} from '../constants';
import {
  previousStep,
  getBranches,
  step3,
} from '../../../redux/checks/checks.actions';
import { getSignaletic } from '../../../redux/accounts/accounts.actions';
const dropdownTemplate = option => (
  <>
    <span style={{ cursor: 'pointer' }}>{`${t(
      `${CHECK_ORDER_STEP3_NAMESPACE}branch`,
    )} ${option.value}`}</span>
  </>
);
class CheckOrderStep3 extends PureComponent {
  state = {
    chosenAgency: null,
  };
  _handleSubmit = formSubmitEvent => {
    formSubmitEvent.preventDefault();
    this.props.step3({
      account: this.props.checkToOrder.accountNumber,
      checkBookType: this.props.checkToOrder.nature,
      number: this.props.checkToOrder.quantity,
      sendingDate: new Date(),
      branch: this.state.chosenAgency,
    });
  };
  componentDidMount() {
    this.props.getBranches();
    if (!this.props.bank) this.props.getSignaletic();
  }
  render() {
    const branchesOptions = this.props.branches
      .filter(branch => branch.bank === this.props.bank)
      .map(branch => ({
        value: branch.name,
        label: branch.name,
      }));
    return (
      <div className="step-pane active" data-step="3">
        <div className="content">
          <h3>{t(`${CHECK_ORDER_STEP3_NAMESPACE}title`)}</h3>
          <form onSubmit={this._handleSubmit}>
            <div className="abone-form">
              <div className="form-pay-txt">
                <div className="form-group select-chequier">
                  <Dropdown
                    name="account"
                    options={branchesOptions}
                    value={this.state.chosenAgency}
                    onChange={e => {
                      console.log(e.value);
                      this.setState({ chosenAgency: e.value });
                    }}
                    placeholder={t(
                      `${CHECK_ORDER_STEP3_NAMESPACE}selectAnAGency`,
                    )}
                    itemTemplate={dropdownTemplate}
                  />
                </div>
              </div>
            </div>
            <div className="form-group form-footer">
              <div className="align-center text-center col-md-12">
                <button
                  className="btn btn-default btn-space"
                  type="button"
                  disabled={this.props.activeStep === 1}
                  onClick={this.props.previousStep}
                >
                  {t(`${CHECK_BOOKS_NAMESPACE}previous`)}
                </button>
                <button
                  className="btn btn-secondary btn-space wizard-next"
                  data-wizard="#wizard1"
                  type="submit"
                >
                  {t(`${CHECK_BOOKS_NAMESPACE}validate`)}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  branches: state.Checks.branches,
  bank: state.Accounts.signaletic.branch.bank,
  checkToOrder: state.Checks.checkToOrder,
});
const mapDispatchToProps = dispatch => ({
  step3: step3Data => {
    dispatch(step3(step3Data));
  },
  getBranches: () => {
    dispatch(getBranches());
  },
  getSignaletic: () => {
    dispatch(getSignaletic());
  },
  previousStep: () => {
    dispatch(previousStep());
  },
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(CheckOrderStep3),
);
