import React, { PureComponent } from 'react';
import { t } from '../../../i18n';
import Stepper from './Stepper';
import CurrentCheckOrderStep from './CurrentCheckOrderStep';
import { CHECKS_NAMESPACE } from '../constants';

class CheckOrder extends PureComponent {
  render() {
    return (
      <div className="panel panel-default">
        <div className="panel-heading panel-heading-divider">
          <div className="row">
            <div className="col-md-6">{t(`${CHECKS_NAMESPACE}orderCheck`)}</div>
          </div>
        </div>
        <div className="panel-body fuelux chequier">
          <div className="wizard wizard-ux no-steps-container" id="wizard1">
            <Stepper />
            <div className="step-content">
              <CurrentCheckOrderStep />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default CheckOrder;
