import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';
import CheckbookIcon from '../../../assets/SvgComponents/ChecksBooking/CheckbookIcon';
import {
  previousStep,
  getBranches,
  step2,
} from '../../../redux/checks/checks.actions';
import {
  CHECK_BOOKS_NAMESPACE,
  CHECK_ORDER_STEP2_NAMESPACE,
} from '../constants';
const checksNatureMock = [
  'Standard (25)',
  'Standard(50)',
  'Premium (25)',
  'Premium (50)',
];
class CheckOrderStep2 extends PureComponent {
  state = {
    nature: checksNatureMock[0],
    quantity: '1',
  };
  _handleSubmit = formSubmitEvent => {
    formSubmitEvent.preventDefault();
    this.props.step2({
      nature: this.state.nature,
      quantity: this.state.quantity,
    });
  };
  render() {
    return (
      <div className="step-pane active" data-step="2">
        <div className="content">
          <h3> {t(`${CHECK_ORDER_STEP2_NAMESPACE}title`)}</h3>
          <form onSubmit={this._handleSubmit}>
            <div className="form-group">
              <label className="col-sm-2">
                {t(`${CHECK_ORDER_STEP2_NAMESPACE}nature`)}
              </label>
              <div className="col-sm-10 line-choice">
                {checksNatureMock.map(nature => (
                  <div className="bcp-radio inline" key={nature}>
                    <input
                      name={nature}
                      id={nature}
                      type="radio"
                      value={nature}
                      onChange={event => {
                        this.setState({ nature: event.target.value });
                      }}
                      checked={nature === this.state.nature}
                    />
                    <label htmlFor={nature}>{nature}</label>
                    <CheckbookIcon />
                  </div>
                ))}
              </div>
            </div>
            <div className="form-group">
              <label className="col-sm-2">
                {t(`${CHECK_ORDER_STEP2_NAMESPACE}number`)}
              </label>
              <div className="col-sm-10 qnty">
                <span>
                  <span
                    className="unstylled_btn operation_btn"
                    id="minus"
                    onClick={() => {
                      if (this.state.quantity > 1) {
                        this.setState({
                          quantity: parseInt(this.state.quantity) - 1,
                        });
                      }
                    }}
                  >
                    -
                  </span>
                  <input
                    className="operation_input align_center"
                    id="carnetValeur"
                    type="text"
                    name="checkbooks_quantity"
                    value={this.state.quantity}
                    onChange={event => {
                      this.setState({ quantity: event.target.value });
                    }}
                  />
                  <span
                    className="unstylled_btn operation_btn"
                    id="plus"
                    onClick={() => {
                      this.setState({
                        quantity: parseInt(this.state.quantity) + 1,
                      });
                    }}
                  >
                    +
                  </span>
                </span>
              </div>
            </div>
            <div className="form-group form-footer">
              <div className="align-center text-center col-md-12">
                <button
                  className="btn btn-default btn-space wizard-previous"
                  onClick={this.props.previousStep}
                >
                  {t(`${CHECK_BOOKS_NAMESPACE}previous`)}
                </button>
                <button
                  className="btn btn-secondary btn-space wizard-next"
                  type="submit"
                >
                  {t(`${CHECK_BOOKS_NAMESPACE}next`)}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  accounts: state.Accounts.accounts,
});
const mapDispatchToProps = dispatch => ({
  step2: step2Data => {
    dispatch(step2(step2Data));
  },
  getBranches: () => {
    dispatch(getBranches());
  },
  previousStep: () => {
    dispatch(previousStep());
  },
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(CheckOrderStep2),
);
