import React from 'react';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';
import FormatDate from '../../../components/FormattedDate/FormattedDate';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import DateCalendar from '../../../components/DateCalendar/DateCalendar';
import OrdersTable from '../../../components/DataTable/CustomDataTable';
import Panel from '../../../components/Panel/Panel';
import { dateFilter } from '../../../utils/DateUtils';
import { getSelectedAccountNumber } from '../../../redux/accounts/accounts.selectors';
import { ORDERS_HISTORY_NAMESPACE } from '../constants';
import { getOrders } from '../../../redux/checks/checks.actions';
import AccountColumn from '../../../components/AccountColumn/AccountColumn';

const CheckBookColumn = props => (
  <div>
    <span className="text-strong">
      {t(`${ORDERS_HISTORY_NAMESPACE}checkBookType`)}
      <span className="text-light">{props.checkBookType}</span>
    </span>
  </div>
);
class OrdersHistory extends React.PureComponent {
  state = {
    maxDate: new Date(),
  };
  componentDidMount() {
    this.props.getOrders();
  }

  _buildDataTableRows = () =>
    this.props.orders.map(data => {
      const { account, checkBookType, number, sendingDate, branch } = data;
      return { account, checkBookType, number, sendingDate, branch };
    });
  _accountTemplate = rowData => (
    <AccountColumn accountNumber={rowData.account} />
  );
  _checkBookTypeTemplate = rowData => (
    <CheckBookColumn checkBookType={rowData.checkBookType} />
  );
  _dateTemplate = rowData => (
    <FormatDate
      dateTime={rowData.sendingDate}
      format={FormatsConfiguration.DateTimeFormat.fullDate}
    />
  );
  _agencyTemplate = rowData => <span>{rowData.branch}</span>;
  _dateCalendarComponent = maxDate => (
    <DateCalendar
      maxDate={maxDate}
      handleDateChange={e => {
        this.dt.filter(e.value, 'date', 'custom');
        this.forceUpdate();
      }}
    />
  );
  render() {
    let rows = this._buildDataTableRows();
    return (
      <div className="page-container_inner">
        <div className="main-content ">
          <div className="row mx-auto">
            <div className="col-sm-12">
              <Panel
                title={t(`${ORDERS_HISTORY_NAMESPACE}title`)}
                selectedAccountNumber={this.props.selectedAccount}
              >
                <OrdersTable
                  value={rows}
                  onRowReorder={e => (rows = e.value)}
                  reference={el => (this.dt = el)}
                  columnResizeMode="fit"
                  cols={[
                    {
                      bodyClassName: 'td-dateclass',
                      field: 'account',
                      header: t(`${ORDERS_HISTORY_NAMESPACE}account`),
                      body: this._accountTemplate,
                    },
                    {
                      field: 'checkBookType',
                      header: t(`${ORDERS_HISTORY_NAMESPACE}checkBookType`),
                      body: this._checkBookTypeTemplate,
                    },
                    {
                      field: 'number',
                      header: t(`${ORDERS_HISTORY_NAMESPACE}number`),
                      className: 'small-col',
                    },
                    {
                      field: 'sendingDate',
                      header: t(`${ORDERS_HISTORY_NAMESPACE}sendingDate`),
                      body: this._dateTemplate,
                      filter: true,
                      filterElement: this._dateCalendarComponent(
                        this.state.maxDate,
                      ),
                      filterFunction: dateFilter,
                    },
                    {
                      field: 'agency',
                      header: t(`${ORDERS_HISTORY_NAMESPACE}agency`),
                      body: this._agencyTemplate,
                    },
                  ]}
                />
              </Panel>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getOrders: () => {
    dispatch(getOrders());
  },
});

const mapStateToProps = state => ({
  orders: state.Checks.orders,
  selectedAccountNumber: getSelectedAccountNumber(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(OrdersHistory),
);
