import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import AccountChoice from './CheckOrderStep1';
import CheckbookChoice from './CheckOrderStep2';
import AgencyChoice from './CheckOrderStep3';

const CurrentCheckOrderStep = props => {
  switch (props.activeStep) {
    case 1:
      return <AccountChoice />;
    case 2:
      return <CheckbookChoice />;
    case 3:
      return <AgencyChoice />;
    default:
      return null;
  }
};
const mapStateToProps = state => ({
  activeStep: state.Checks.activeStep,
});
export default withTranslation()(
  connect(
    mapStateToProps,
    null,
  )(CurrentCheckOrderStep),
);
